import React, { Component } from 'react'
import {
    Container,
    Row,
    Col,
    Modal,
    Button
} from 'react-bootstrap'
import './Resume.css'
import './Experience.css'
import AMBBrandBook from '../asset/AMBUSH-Brand-Book-Final-Cover.png'
import AMBLookBook from '../asset/AMBUSH-Lookbook-Final-Cover.png'
import v1 from '../asset/v1.png'
import v2 from '../asset/v2.png'
import v3 from '../asset/v3.png'



export default class Projects extends Component {
    constructor(props) {
        super(props)

        this.state = {
            ShowModal: false,
            DisplayContent: null
        }


    }

    componentDidMount = () => {

    }

    RenderDocs = () => {
        return (<iframe style={{height: '85%'}} src={"https://docs.google.com/viewer?url=" + this.state.DisplayContent + "&embedded=true"} title="file" width="100%" height="500"  />)
    }

    ToggleModal = (show) => { 
        this.setState({ShowModal: show})
    }


    DisplayPDF = (title, content) => {
        this.setState({DisplayContent: content, ContentTitle: title})
        this.ToggleModal(true)
    }

    render() {
        return (
            <Container style={{ marginTop: "20px" }}>
                <Row>
                    <Col sm={12} md={12} lg={12} style={{ display: "flex" }}>
                        <h2 style={{ margin: "auto", marginBottom: "40px" }}>Personal Projects</h2>
                    </Col>
                </Row>
                <Row id="custom-row">
                    <Col id="project-row" sm={12} md={12} lg={12}>
                        <h2>AMBUSH Brand Book Final</h2>
                        <img id="project-img" src={AMBBrandBook} alt="AMBUSH Brand Book Final" />
                        <hr />
                        <Button variant="success" onClick={() => {this.DisplayPDF("AMBUSH Brand Book Final", "https://github.com/CccrizzZ/MSTResume/raw/main/mst-resume/src/asset/AMBUSH-Brand-Book-Final.pdf")}}>Show Full PDF Book</Button>
                    </Col>
                </Row>
                <Row id="custom-row">
                    <Col id="project-row" sm={12} md={12} lg={12}>
                        <h2>AMBUSH Look Book Final</h2>
                        <img id="project-img" src={AMBLookBook} alt="AMBUSH Look Book Final" />
                        <hr />
                        <Button variant="success" onClick={() => {this.DisplayPDF("AMBUSH Look Book Final", "https://github.com/CccrizzZ/MSTResume/raw/main/mst-resume/src/asset/AMBUSH-Lookbook-Final.pdf")}}>Show Full PDF Book</Button>
                    </Col>
                </Row>
                <Row id="custom-row">
                    <Col id="project-row" sm={12} md={12} lg={12}>
                        <h2>ROBERTO CAVALLI VOLUME I - PAST</h2>
                        <img id="project-img" src={v1} alt="ROBERTO CAVALLI VOLUME I - PAST"/>
                        <hr />
                        <Button variant="success" onClick={() => {this.DisplayPDF("ROBERTO CAVALLI VOLUME I - PAST", "https://github.com/CccrizzZ/MSTResume/raw/main/mst-resume/src/asset/ROBERTO%20CAVALLI%20VOLUME%20I%20-%20PAST.pdf")}}>Show Full PDF Book</Button>

                    </Col>
                </Row>
                <Row id="custom-row">
                    <Col id="project-row" sm={12} md={12} lg={12}>
                        <h2>ROBERTO CAVALLI VOLUME II - PRESENT</h2>
                        <img id="project-img" src={v2} alt="ROBERTO CAVALLI VOLUME II - PRESENT"/>
                        <hr />
                        <Button variant="success" onClick={() => {this.DisplayPDF("ROBERTO CAVALLI VOLUME II - PRESENT", "https://github.com/CccrizzZ/MSTResume/raw/main/mst-resume/src/asset/ROBERTO%20CAVALLI%20VOLUME%20II%20-%20PRESENT.pdf")}}>Show Full PDF Book</Button>

                    </Col>
                </Row>
                <Row id="custom-row">
                    <Col id="project-row" sm={12} md={12} lg={12}>
                        <h3>ROBERTO CAVALLI VOLUME III - FUTURE</h3>
                        <img id="project-img" src={v3} alt="ROBERTO CAVALLI VOLUME III - FUTURE"/>
                        <hr />
                        <Button variant="success" onClick={() => {this.DisplayPDF("ROBERTO CAVALLI VOLUME III - FUTURE", "https://github.com/CccrizzZ/MSTResume/raw/main/mst-resume/src/asset/ROBERTO%20CAVALLI%20VOLUME%20III%20-%20FUTURE.pdf")}}>Show Full PDF Book</Button>

                    </Col>
                </Row>




                {/* overlay modal */}
                <Modal show={this.state.ShowModal} onHide={()=>{this.ToggleModal(false)}} backdrop="static" keyboard={false}>

                    <div style={{color: 'white', paddingTop: "40px", width: '100%', height: '90vh', textAlign: 'center', padding: '10px', backgroundColor: '#251D3A'}}>
                        <h4>{this.state.ContentTitle == null ? null : this.state.ContentTitle}</h4>
                        {this.state.DisplayContent == null ? null : this.RenderDocs()}
                        <Button style={{minWidth:"60%", position: "absolute", bottom: "0", left: "0", right: "0"}} variant="dark" size="lg" onClick={()=>{this.ToggleModal(false)}}>Close</Button>
                    </div>

                </Modal>
            </Container>
        )
    }
}
